//import logo from './logo.svg';
import React, {Component} from 'react';
import Header from './Components/Header';
import Nav from './Components/Nav';
import ReadContents from './Components/ReadContents';
import CreateContents from './Components/CreateContents';
import UpdateContents from './Components/UpdateContents';
import ControlContents from './Components/ControlContents';
import './App.css';

class App extends Component {
    
    constructor (props) {
      super(props);
      this.max_contented_id = 4;
      this.state = {
        mode : 'read',
        selected_contented_id : 1,
        welcome : {
          title:"WELCOME,",
          desc:"서비스를 이용해주셔서 감사합니다."
        },
        header : {
          title:"WEB",
          desc:"super님을 위한 강의입니다."
        },
        contents : [
          {id:1, title:"HTML", desc:"HTML에 관한 강의입니다."},
          {id:2, title:"CSS", desc:"CSS에 관한 강의입니다."},
          {id:3, title:"JavsScript", desc:"JavsScript에 관한 강의입니다."},
          {id:4, title:"JAVA", desc:"JAVA에 관한 강의입니다."}
        ]
      }
    }
    
    getReadContents() {
        var i = 0;
        while (i < this.state.contents.length) {
        var data = this.state.contents[i];
        if(data.id === this.state.selected_contented_id) {
          //_title = data.title;
          //_desc = data.desc;
          return data;
        }
        i ++;
      }
    }

    render(){
      
      var _title,_desc,_article = null;
      if (this.state.mode === 'welcome') {
        _title = this.state.welcome.title;  
        _desc = this.state.welcome.desc; 
        _article = <ReadContents
                        title={_title}
                        desc={_desc}
                     >
                     </ReadContents> 
      } else if (this.state.mode === 'read') {
        //_title = this.state.contents[1].title;
        //_desc = this.state.contents[1].desc;
        /*
        var i = 0;
        while (i < this.state.contents.length) {
          
          var data = this.state.contents[i];
          if(data.id === this.state.selected_contented_id) {
            _title = data.title;
            _desc = data.desc;
            break;
          }
          i ++;
        }
        */

        var _content = this.getReadContents();

          _article = <ReadContents
                        title={_content.title}
                        desc={_content.desc}
                     >
                     </ReadContents>

      } else if (this.state.mode === 'create') {
          _article = <CreateContents
                        title={_title}
                        desc={_desc}
                        onSubmit={function(_title,_desc){
                          this.max_contented_id = this.max_contented_id+1;
                          //console.log(_title,_desc);
                          
                          // 기존 리스트 복제
                          var _contents = Array.from(this.state.contents);
                          
                          _contents.push(
                            {
                              id:this.max_contented_id,
                              title:_title,
                              desc:_desc
                            }
                          );
                          
                          this.setState({
                            contents: _contents,
                            mode: 'read',
                            selected_contented_id: this.max_contented_id
                          });
                        }.bind(this)}
                     >
                     </CreateContents>
        
      } else if (this.state.mode === 'update') {
        _content = this.getReadContents();
        _article = <UpdateContents
                      //title={_content.title}
                      //desc={_content.desc}
                      data={_content}
                      onSubmit={function(_id,_title,_desc){
                        //console.log(_id,_title,_desc);

                        var _contents = Array.from(this.state.contents);
                        var i = 0;
                        while(i < this.state.contents.length) {

                          if(_contents[i].id === _id) {
                            _contents[i] = {
                              id:_id,
                              title:_title,
                              desc:_desc
                            }
                            break;
                          }
                          i++;
                        }
                        
                        this.setState ({
                          contents: _contents,
                          mode: 'read'
                        })

                      }.bind(this)}
                   >

                   </UpdateContents>
      }

      return (
      <div className="App">
        <Header 
                title={this.state.header.title} 
                desc={this.state.header.desc}
                onChangedPage={function(){
                  this.setState({
                    mode:'welcome'
                  });
                }.bind(this)}
        >
        </Header>
        <Nav
            data={this.state.contents}
            onChangedPage={function(id){
              //console.log(id);
              this.setState({
                mode:'read',
                selected_contented_id: Number(id)
              });
            }.bind(this)}      
        >
        </Nav>
        <ControlContents
          onChangedMode={function(_mode){

            // 삭제기능
            
            //console.log(_mode);
            if(_mode === 'delete') {
              if(window.confirm('delete OK?')) {
                var _content = Array.from(this.state.contents);
                var i = 0;

                while(i < this.state.contents.length) {
                  if(_content[i].id === this.state.selected_contented_id) {
                    _content.splice(i,1);
                    break;
                  }
                  i++;
                }
                alert('delete complete!')
              }

              this.setState({
                mode:'welcome',
                contents:_content
              })

            } else {
              this.setState({
                mode: _mode
              });
            }
            //console.log(_mode);
          }.bind(this)}
        >
        </ControlContents>
        {_article}
      </div>
    );
  }
  
}

export default App;
