import React, {Component}  from 'react';

class UpdateContents extends Component {

  constructor(props) {
    super(props);
    this.state = {
      id: this.props.data.id,
      title: this.props.data.title,
      desc: this.props.data.desc
    }
  }

  render() {
    //console.log(this.props.data);
    return (
      <article>
        <h3>UPDATE</h3>
        <form
          action="/update_process"
          method="post"
          onSubmit={function(e){
            e.preventDefault();
            this.props.onSubmit(
              this.state.id,
              this.state.title,
              this.state.desc
            );
            //console.log(e.target.title.value);
          }.bind(this)}
        >
          <p>
            <input 
              type="hidden"
              placeholder="id"
              name="id"
              value={this.state.id} 
            />
          </p>
          <p>
            <input 
              type="text"
              placeholder="title"
              name="title"
              value={this.state.title} 
              onChange={function(e){
                this.setState({
                  title: e.target.value
                });
              }.bind(this)}
            />
          </p>
          <p>
            <textarea
              placeholder="description"
              name="desc"
              value={this.state.desc}
              onChange={function(e){
                this.setState({
                  desc: e.target.value
                });
              }.bind(this)}
            />
          </p>
          <p>
            <input 
              type="submit"
              value="submit"
            />
          </p>
        </form>
      </article>
    )
  }

}

export default UpdateContents;