import React, {Component}  from 'react';

class CreateContents extends Component {

  render() {
    return (
      <article>
        <h3>CREATE</h3>
        <form
          action="/create_process"
          method="post"
          onSubmit={function(e){
            e.preventDefault();
            this.props.onSubmit(
              e.target.title.value,
              e.target.desc.value
            );
            //console.log(e.target.title.value,);
          }.bind(this)}
        >
          <p>
            <input 
              type="text"
              placeholder="title"
              name="title" 
            />
          </p>
          <p>
            <textarea
              placeholder="description"
              name="desc"
            />
          </p>
          <p>
            <input 
              type="submit"
              value="submit"
            />
          </p>
        </form>
      </article>
    )
  }

}

export default CreateContents;