import React, {Component} from 'react';


class Header extends Component {

  render () {
    return (
      <header>
        <h1>
          <a 
            href="/web.html"
            onClick={function(e){
              e.preventDefault();
              this.props.onChangedPage();
            }.bind(this)}
          >
            {this.props.title}
          </a>
        </h1>
        <p><strong>{this.props.desc}</strong></p>
      </header>
    );
  }

}

export default Header;