import React, {Component} from 'react';

class ControlContents extends Component {
  render () {
    return (
      <article>
        <ul>
          <li>
            <a 
              href="/create.html"
              onClick={function(e){
                e.preventDefault();
                var createMode = 'create';
                this.props.onChangedMode(createMode);
              }.bind(this)}
            >
              CREATE
            </a>
          </li>
          <li>
            <a 
              href="/update.html"
              onClick={function(e){
                e.preventDefault();
                var updateMode = 'update';
                this.props.onChangedMode(updateMode);
              }.bind(this)}
            >
              UPDATE
            </a>
          </li>
          <li>
            <input 
              type="button" 
              value="DELETE"
              onClick={function(e){
                e.preventDefault();
                var deleteMode = 'delete';
                this.props.onChangedMode(deleteMode);
              }.bind(this)}
            >
            </input>
          </li>
        </ul>
      </article>
    );
  }
}

export default ControlContents;