import React, {Component} from 'react';

class Nav extends Component {
  
  render () {
    
    var data = this.props.data;
    var lists = [];
    var i = 0;
    //console.log("data입",data);

    while (i < data.length) {
      lists.push(
        <li key={data[i].id}>
            <a 
              href={"/contents/"+ data[i].id}
              onClick={function(e){
                e.preventDefault();
                //console.log(e);
                //console.log(e.target.dataset.id);
                this.props.onChangedPage(e.target.dataset.id);
              }.bind(this)}
              data-id={data[i].id}
            > 
              {data[i].title}
            </a>
        </li>
      );

      i++;
    }

    return (
      <nav>
        <ul>
          {lists}
        </ul>
      </nav>
    )
  }

}

export default Nav;